    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body, button {
    color: rgba(0, 0, 0, 0.9);
}

body {
    font-family: 'Roboto', 'Open Sans', 'Helvetica Neue', 'Segoe-UI', sans-serif;
    min-height: 100vh;
    display: grid;
    grid-template-rows: auto 1fr auto;
}

ul.nav-list {
    list-style: none;
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 6rem;
    align-items: center;
}

ul.nav-list > div.links {
    display: flex;
    gap: 2rem;
    font-weight: 500;
}

li.nav-logo {
    font-family: 'Pacifico', 'Roboto', 'Open Sans', 'Helvetica Neue', 'Segoe-UI', sans-serif;
    font-size: 2.5rem;
}

li.nav-item {
    cursor: pointer;
    font-size: 1.05rem;
}

ul.nav-list a {
    text-decoration: none;
    color: inherit;
}

.homepage {
    display: grid;
    grid-template-columns: 10fr 9fr;
    padding: 0 4rem;
}

.page {
    width: 100%;
    height: 100%;
}

.homepage > .left-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    text-align: left;
}

.bold-big {
    font-size: 3rem;
    font-weight: 700;
    padding: 2rem 0 1rem;
}

.faded-small {
    margin-left: 0.5rem;
}

.faded-small {
    font-size: 1rem;
    color: #333;
    margin-bottom: 1rem;
}

.homepage > .right-content {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: no-repeat;
    background-size: cover;
}

button {
    cursor: pointer;
}

button, a.icon > svg, li.nav-item {
    transition: 100ms all ease-in-out;
}

button:not(:disabled):hover, a.icon > svg:hover {
    transform: scale(102%);
}

button:not(:disabled):active, a.icon > svg:active {
    transform: scale(99%);
}

li.nav-item:hover {
    transform: scale(110%);
}

li.nav-item.active {
    font-weight: 600;
}

li.nav-item.active:not(:hover) {
    transform: scale(105%) translateY(-0.5px);
}

li.nav-item::after {
    content: '';
    position: relative;
    top: 1px;
    display: block;
}

li.nav-item.active::after {
    height: 0;
    width: 100%;
    border-bottom: 1.5px solid rgba(0, 0, 0, 0.9);
}

.discover-button {
    padding: 0.7rem 1rem;
    font-size: 1.1rem;
    font-weight: 500;
    border: none;
    border-radius: 6px;
}

footer {
    padding: 1rem;
    text-align: center;
}

a > svg {
    position: relative;
    top: 0.3em;
    height: 1.4em;
}

.menupage {
    display: grid;
    grid-template-columns: 7fr 10fr;
    gap: 3rem;
    padding: 0 4rem;
}

.menupage > .left-content {
    margin-top: 0.5rem;
    background: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-repeat: no-repeat;
    background-size: cover;
}

.menu {
    overflow: auto;
}

.menu > h2, .about > h2 {
    text-align: center;
    margin: 1rem auto;
    font-size: 1.7rem;
    font-weight: 700;
}

.menu-item-group > h3 {
    font-size: 1.2rem;
    font-weight: 100;
    color: #030712;
    margin-bottom: 0.5rem;
}

.menu-item-group > .items {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    padding-left: 1rem;
}

.item {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 3rem;
}

.item .name {
    font-weight: 400;
    display: block;
}

.item .info {
    font-size: smaller;
    font-weight: 300;
    opacity: 0.85;
}

.price {
    height: 100%;
    align-self: flex-start;
}

.menu-item-group + .menu-item-group {
    margin-top: 1rem;
}

.about {
    padding: 5rem 2rem;
    text-align: center;
    background: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    box-shadow: inset 0 0 0 2000px rgba(0, 0, 0, 0.6);
    color: #eee;
    margin-bottom: 1rem;
}

.about p {
    font-size: 1.2rem;
    line-height: 1.4;
    font-weight: 500;
}

.information {
    text-align: center;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding: 2rem;
    align-items: center;
}

.open-hours > h2 {
    margin-bottom: 1rem;
}

.time + .time {
    margin-top: 1rem;
}

a {
    color: inherit;
    text-decoration: none;
}`, "",{"version":3,"sources":["webpack://./src/assets/style.css"],"names":[],"mappings":"AAEA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,4EAA4E;IAC5E,iBAAiB;IACjB,aAAa;IACb,iCAAiC;AACrC;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,8BAA8B;IAC9B,oBAAoB;IACpB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,gBAAgB;AACpB;;AAEA;IACI,wFAAwF;IACxF,iBAAiB;AACrB;;AAEA;IACI,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,qBAAqB;IACrB,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,+BAA+B;IAC/B,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,uBAAuB;IACvB,gBAAgB;AACpB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,oBAAoB;AACxB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,WAAW;IACX,mBAAmB;AACvB;;AAEA;IACI,mDAA6B;IAC7B,4BAA4B;IAC5B,sBAAsB;AAC1B;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,iCAAiC;AACrC;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,yCAAyC;AAC7C;;AAEA;IACI,WAAW;IACX,kBAAkB;IAClB,QAAQ;IACR,cAAc;AAClB;;AAEA;IACI,SAAS;IACT,WAAW;IACX,6CAA6C;AACjD;;AAEA;IACI,oBAAoB;IACpB,iBAAiB;IACjB,gBAAgB;IAChB,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,+BAA+B;IAC/B,SAAS;IACT,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,mDAAyB;IACzB,4BAA4B;IAC5B,sBAAsB;AAC1B;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;IACjB,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,cAAc;IACd,qBAAqB;AACzB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,qBAAqB;IACrB,SAAS;AACb;;AAEA;IACI,gBAAgB;IAChB,cAAc;AAClB;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,aAAa;AACjB;;AAEA;IACI,YAAY;IACZ,sBAAsB;AAC1B;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;IAClB,kBAAkB;IAClB,mDAAiC;IACjC,4BAA4B;IAC5B,sBAAsB;IACtB,4BAA4B;IAC5B,iDAAiD;IACjD,WAAW;IACX,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,qCAAqC;IACrC,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,cAAc;I